import React, { useRef, useEffect } from 'react';
import * as THREE from 'three';


function ThreeScene({ images, dalleImage, images_murs }) {

  console.log(images_murs);

  const mountRef = useRef(null);
  const mouse = useRef({ x: 0, y: 0 });
  const keysPressed = {};

  const touchStart = useRef({ x: 0, y: 0 });
  const touchCurrent = useRef({ x: 0, y: 0 });

  useEffect(() => {
    const scene = new THREE.Scene();
    const loader = new THREE.TextureLoader();

    // Charger la texture de dalle pour le sol
    const dalleTexture = loader.load(dalleImage, (tex) => {
      tex.wrapS = tex.wrapT = THREE.RepeatWrapping;
      tex.repeat.set(10, 10); // Rpter la texture 10 fois en largeur et en profondeur
    });

    // Cration du renderer avec antialias et activation de la correction gamma pour le rendu
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.outputEncoding = THREE.sRGBEncoding; // Correction gamma pour le rendu



    // Crer le mesh du sol avec la gomtrie et le matriau

    // Chargement des textures avec correction gamma et application de THREE.LinearMipmapLinearFilter
    const textures = images.map(image => {
      const texture = loader.load(image, (tex) => {
        tex.encoding = THREE.sRGBEncoding; // Appliquer l'encodage sRGB pour chaque texture
        tex.minFilter = THREE.LinearMipmapLinearFilter; // Appliquer le filtre de minification
      });
      return texture;
    });

    // Assurez-vous d'avoir gnr des mipmaps pour les textures o vous utilisez ce filtre.
    textures.forEach(texture => {
      texture.generateMipmaps = true; // Assure que les mipmaps sont gnrs pour les textures
    });

    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);




    mountRef.current.appendChild(renderer.domElement);


    let nombrePetitsCubes = 10; // Nombre de petits cubes
    const espacementEntreCubes = 50; // Espacement entre les cubes
    const petitCubeDepth = espacementEntreCubes; // Profondeur d'un petit cube
    const petitCubeWidth = espacementEntreCubes;
    const cubeHeight = 50;

    // Calcul de la profondeur totale ncessaire
    const profondeurTotale = (petitCubeDepth * nombrePetitsCubes) + ((espacementEntreCubes) * (nombrePetitsCubes - 1)) + (3 * espacementEntreCubes)

    const largeurTotale = (petitCubeWidth * nombrePetitsCubes) + ((espacementEntreCubes) * (nombrePetitsCubes - 1)) + (2 * espacementEntreCubes);
    const cubeWidth = Math.max(largeurTotale, 10); // Assurez-vous que la largeur est suffisante


    // Assurez-vous que le grand cube a une profondeur suffisante pour contenir tous les petits cubes
    const cubeDepth = Math.max(profondeurTotale, 10); // Choisissez une valeur minimale approprie

    const solMaterial = new THREE.MeshBasicMaterial({ map: dalleTexture });

    // Crer la gomtrie du sol
    const solGeometry = new THREE.PlaneGeometry(cubeWidth, cubeDepth);

    const sol = new THREE.Mesh(solGeometry, solMaterial);
    sol.rotation.x = -Math.PI / 2; // Faire pivoter le sol pour qu'il soit horizontal
    sol.position.y = -(cubeHeight / 2); // Positionner le sol  la base du cube principal

    // Ajouter le sol  la scne
    scene.add(sol);




    // const cubeDepth = 100;


    camera.position.set(0, 0, (cubeDepth / 2));


    const materials = [
      new THREE.MeshBasicMaterial({ color: 0x000000, side: THREE.BackSide, transparent: true, opacity: 0 }), // Droite
      new THREE.MeshBasicMaterial({ color: 0x000000, side: THREE.BackSide, transparent: true, opacity: 0 }), // Gauche
      new THREE.MeshBasicMaterial({ color: 0x87CEEB, side: THREE.BackSide, transparent: false, opacity: 1 }), // Haut (plafond, bleu ciel)
      new THREE.MeshBasicMaterial({ color: 0x000000, side: THREE.BackSide, transparent: true, opacity: 0 }), // Bas (sol)
      new THREE.MeshBasicMaterial({ color: 0x000000, side: THREE.BackSide, transparent: true, opacity: 0 }), // Avant
      new THREE.MeshBasicMaterial({ color: 0x000000, side: THREE.BackSide, transparent: true, opacity: 0 })  // Arrire
    ];

    // const materials = [
    //   new THREE.MeshBasicMaterial({ color: 0x5555ff, side: THREE.BackSide }), // Face droite
    //   new THREE.MeshBasicMaterial({ color: 0x55ff55, side: THREE.BackSide }), // Face avant
    //   new THREE.MeshBasicMaterial({ color: 0xff55ff, side: THREE.BackSide }),  // Face gauche
    //   new THREE.MeshBasicMaterial({ color: 0xff5555, side: THREE.BackSide }), // Haut
    //   new THREE.MeshBasicMaterial({ transparent: true, opacity: 0 }), // Bas (sol), rendu transparent
    //   new THREE.MeshBasicMaterial({ color: 0x55ffff, side: THREE.BackSide }), // Face arrire
    // ];




    const nbImagesLargeur = nombrePetitsCubes + espacementEntreCubes * (nombrePetitsCubes + 1)
    const nbImagesProfondeur = profondeurTotale / petitCubeWidth;

    // Prparation des textures pour les murs
    const murTextures = images_murs.map(src => loader.load(src));

    // Appliquer les textures de mur en boucle
    let murIndex = 0; // Index pour parcourir murTextures en boucle
    for (let i = 0; i < materials.length; i++) {
      if (i !== 2 && i !== 4) { // Exclure le haut et le bas du cube
        materials[i] = new THREE.MeshBasicMaterial({ map: murTextures[murIndex % murTextures.length] });
        murIndex++;
      }
    }

    const geometry = new THREE.BoxGeometry(cubeWidth, cubeHeight, cubeDepth);
    const cube = new THREE.Mesh(geometry, materials);
    scene.add(cube);

    // Les panneaux de mur ne devraient pas craser la couleur du haut du cube,
    // donc cette partie n'a pas besoin d'tre modifie par rapport  votre demande.
    // Assurez-vous simplement que le traitement des textures des murs n'interfre pas
    // avec la couleur spcifie pour le haut du cube.

    // Le reste de votre code pour ajouter des panneaux aux murs peut rester inchang.
    // Assurez-vous simplement que la logique d'application des textures murales ne modifie pas
    // les matriaux destins  d'autres parties du cube, en particulier le haut.


    // Ici, commencez  ajouter le code pour les panneaux de mur
    const panneauWidth = petitCubeWidth; // Utilisez la mme largeur que celle des petits cubes
    const panneauHeight = cubeHeight; // La hauteur du panneau est la mme que celle du cube principal

    // Supposons que murTextures est dj dfini comme montr prcdemment dans votre code
    // Si murTextures n'est pas encore dfini, faites-le ici

    // Pour chaque mur, crez et positionnez les panneaux
    // Exemple pour un seul mur, rptez pour les autres murs avec des ajustements
    let posX = -(cubeWidth / 2) + (panneauWidth / 2);
    const posY = 0; // Centre en hauteur du mur
    const posZ = -(cubeDepth / 2); // Ajustez en fonction du mur sur lequel vous travaillez

    // Supposons que vous avez dj charg murTextures comme montr prcdemment


    // Mur avant

    for (let i = 0; i < nbImagesLargeur; i++) {
      let texture = murTextures[(murIndex++) % murTextures.length];
      let material = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });

      let panneauGeometry = new THREE.PlaneGeometry(panneauWidth, panneauHeight);
      let panneau = new THREE.Mesh(panneauGeometry, material);

      let posX = -(cubeWidth / 2) + panneauWidth / 2 + i * panneauWidth; // Correction pour l'orientation et l'espacement
      let posY = 0;
      let posZ = cubeDepth / 2;

      panneau.position.set(posX, posY, -posZ);
      panneau.lookAt(new THREE.Vector3(posX, posY, -posZ + 1)); // Correctement orient vers l'intrieur
      scene.add(panneau);
    }

    // Pour le mur droit
    for (let j = 0; j < nbImagesProfondeur; j++) {
      let texture = murTextures[(murIndex++) % murTextures.length];
      let material = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });
      let panneauGeometry = new THREE.PlaneGeometry(panneauWidth, panneauHeight);
      let panneau = new THREE.Mesh(panneauGeometry, material);

      let posX = cubeWidth / 2; // Ajustez cette valeur pour positionner correctement le panneau
      let posY = 0; // Ajustez en fonction de la hauteur dsire
      let posZ = -(cubeDepth / 2) + panneauWidth / 2 + j * panneauWidth;

      panneau.position.set(posX, posY, posZ);
      panneau.lookAt(new THREE.Vector3(posX + 1, posY, posZ)); // Regarde vers l'intrieur du cube
      scene.add(panneau);
    }

    // Pour le mur gauche
    for (let k = 0; k < nbImagesProfondeur; k++) {
      let texture = murTextures[(murIndex++) % murTextures.length];
      let material = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });
      let panneauGeometry = new THREE.PlaneGeometry(panneauWidth, panneauHeight);
      let panneau = new THREE.Mesh(panneauGeometry, material);

      let posX = -cubeWidth / 2; // Ajustez cette valeur pour positionner correctement le panneau
      let posY = 0; // Ajustez en fonction de la hauteur dsire
      let posZ = -(cubeDepth / 2) + panneauWidth / 2 + k * panneauWidth;

      panneau.position.set(posX, posY, posZ);
      panneau.lookAt(new THREE.Vector3(posX - 1, posY, posZ)); // Regarde vers l'intrieur du cube
      scene.add(panneau);
    }


    // Pour le mur arrire
    for (let i = 0; i < nbImagesLargeur; i++) {
      let texture = murTextures[(murIndex++) % murTextures.length];
      let material = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });
      let panneauGeometry = new THREE.PlaneGeometry(panneauWidth, panneauHeight);
      let panneau = new THREE.Mesh(panneauGeometry, material);

      // Positionnement pour le mur arrire, ajustant posX pour s'aligner sur la largeur du cube
      let posX = -(cubeWidth / 2) + panneauWidth / 2 + i * panneauWidth; // Utiliser panneauWidth pour un espacement uniforme
      let posY = 0; // Centre vertical du panneau
      let posZ = (cubeDepth / 2); // Position Z pour le mur arrire

      panneau.position.set(posX, posY, posZ);
      // Orienter le panneau vers l'extrieur du cube ( l'oppos de l'orientation du mur avant)
      panneau.lookAt(new THREE.Vector3(posX, posY, posZ - 1));

      scene.add(panneau);
    }








    // Petit cube

    const petitCubeHeight = cubeHeight;
    // const petitCubeDepth = 5;

    // const petitCubePosX = 0;
    const petitCubePosY = 0;

    const smallCubeGeometry = new THREE.BoxGeometry(petitCubeWidth, petitCubeHeight, petitCubeDepth);


    // ...

    const petitsCubesPositions = [];

    ///// Degrad //// 

    // Dfinition de deux ensembles de matriaux
    function generateGradientTexture() {
      // Crer un lment canvas
      const canvas = document.createElement('canvas');
      canvas.width = 256; // Largeur du canvas pour le dgrad
      canvas.height = 256; // Hauteur du canvas pour le dgrad

      const context = canvas.getContext('2d');

      // Crer le dgrad
      const gradient = context.createLinearGradient(0, 0, canvas.width, canvas.height);
      gradient.addColorStop(0, 'blue'); // Finit en bleu
      gradient.addColorStop(0.3, '#aaaaaa'); // Commence en rouge
      gradient.addColorStop(0.6, '#888888'); // Change en vert
      gradient.addColorStop(1, 'blue'); // Finit en bleu

      // Appliquer le dgrad au contexte
      context.fillStyle = gradient;
      context.fillRect(0, 0, canvas.width, canvas.height);

      // Utiliser le canvas comme texture
      const texture = new THREE.Texture(canvas);
      texture.needsUpdate = true; // Indiquer  Three.js que la texture doit tre mise  jour

      return texture;
    }

    // Utiliser la texture de dgrad pour les matriaux
    const gradientTexture = generateGradientTexture();
    const materialWithGradient = new THREE.MeshBasicMaterial({ map: gradientTexture });

    // Appliquer ce matriau  vos petits cubes
    const smallCubeMaterialsPair = Array(6).fill(materialWithGradient);
    const smallCubeMaterialsImpair = Array(6).fill(materialWithGradient);


    ///// Fin du dgrad



    // Fonction pour diviser les textures en sous-tableaux de 4 lments
    function chunkTextures(textures, chunkSize) {
      let result = [];
      for (let i = 0; i < textures.length; i += chunkSize) {
        result.push(textures.slice(i, i + chunkSize));
      }
      return result;
    }

    // Diviser les textures en sous-tableaux de 4 lments
    const chunkedTextures = chunkTextures(textures, 4);

    // Boucle pour crer les petits cubes
    for (let i = 0; i < nombrePetitsCubes; i++) {
      let cubesDansRange = nombrePetitsCubes;
      if (nombrePetitsCubes > 2 && i % 2 === 0) {
        cubesDansRange += 1; // Ajoute un cube supplmentaire pour les ranges paires si nombrePetitsCubes > 2
      }
      for (let j = 0; j < cubesDansRange; j++) {
        let petitCubePosX;
        let petitCubePosZ;

        // Position de chaque petit cube
        if (nombrePetitsCubes <= 2) {
          // S'il y a 2 petits cubes ou moins, ajouter un espace avant le premier cube
          petitCubePosX = -(cubeWidth / 2) + petitCubeWidth / 2 + espacementEntreCubes + (j * (petitCubeWidth + espacementEntreCubes));
        } else {
          // Pour plus de 2 petits cubes, la position habituelle
          petitCubePosX = (i % 2 === 0) ? -(cubeWidth / 2) + petitCubeWidth / 2 + (j * (petitCubeWidth + espacementEntreCubes))
            : -(cubeWidth / 2) + petitCubeWidth / 2 + espacementEntreCubes + (j * (petitCubeWidth + espacementEntreCubes));
        }

        petitCubePosZ = -(cubeDepth / 2) + petitCubeDepth / 2 + espacementEntreCubes + (i * (petitCubeDepth + espacementEntreCubes));

        // Choix du matriau en fonction de la parit de la range
        const materials = i % 2 === 0 ? smallCubeMaterialsPair : smallCubeMaterialsImpair;

        // Crer et positionner le petit cube
        const smallCube = new THREE.Mesh(smallCubeGeometry, materials);
        smallCube.position.set(petitCubePosX, petitCubePosY, petitCubePosZ);
        scene.add(smallCube);

        const edges = new THREE.EdgesGeometry(smallCubeGeometry);
        const line = new THREE.LineSegments(edges, new THREE.LineBasicMaterial({ color: 0x000 })); // Mettez la couleur souhaite pour les artes
        line.position.copy(smallCube.position); // Positionner les lignes au mme endroit que le cube
        scene.add(line);

        //  l'intrieur de la boucle for o vous crez les petits cubes
        const smallCubeCenter = new THREE.Vector3(); // Centre du petit cube
        smallCubeCenter.set(petitCubePosX, petitCubePosY, petitCubePosZ);

        const miniCubeWidth = 40; // Largeur du mini-cube
        const miniCubeHeight = 40; // Hauteur du mini-cube
        const miniCubeDepth = 2; // Profondeur du mini-cube
        const miniCubeGeometry = new THREE.BoxGeometry(miniCubeWidth, miniCubeHeight, miniCubeDepth);

        // Dfinition des matriaux pour chaque face du mini-cube


        // Position des mini-cubes sur chaque face du petit cube
        const positions = [
          // Exclure Haut et Bas ici
          new THREE.Vector3(smallCubeCenter.x + petitCubeWidth / 2, smallCubeCenter.y, smallCubeCenter.z), // Droite
          new THREE.Vector3(smallCubeCenter.x - petitCubeWidth / 2, smallCubeCenter.y, smallCubeCenter.z), // Gauche
          new THREE.Vector3(smallCubeCenter.x, smallCubeCenter.y, smallCubeCenter.z + petitCubeDepth / 2), // Avant
          new THREE.Vector3(smallCubeCenter.x, smallCubeCenter.y, smallCubeCenter.z - petitCubeDepth / 2)  // Arrire
        ];

        // Cration et ajout des mini-cubes avec des couleurs diffrentes pour chaque face
        //  l'intrieur de la boucle for o vous crez et positionnez les mini-cubes
        // Matriaux pour les mini-cubes
        const faceParalleleMaterial = new THREE.MeshBasicMaterial({ color: 0x0000ff }); // Bleu pour la face parallle
        const autreFaceMaterial = new THREE.MeshBasicMaterial({ color: 0xffff00 }); // Jaune pour les autres faces

        // Obtenir le sous-tableau de textures pour le cube actuel
        const currentTextures = chunkedTextures[(i * nombrePetitsCubes + j) % chunkedTextures.length];



        positions.forEach((pos, index) => {
          // Crer un tableau de matriaux pour chaque mini-cube
          let miniCubeMaterials = [
            autreFaceMaterial,
            autreFaceMaterial,
            autreFaceMaterial,
            autreFaceMaterial,
            new THREE.MeshBasicMaterial({ map: currentTextures[index % currentTextures.length] }),
            autreFaceMaterial,
          ];


          // Cration du mini-cube avec le tableau de matriaux ajust
          const miniCube = new THREE.Mesh(miniCubeGeometry, miniCubeMaterials);
          miniCube.position.set(pos.x, pos.y, pos.z);

          // Ajuster l'orientation du mini-cube si ncessaire
          switch (index) {
            case 0: // Droite
              miniCube.rotation.y = Math.PI / 2; // Rotation de 90 degrs autour de l'axe Y
              break;
            case 1: // Gauche
              miniCube.rotation.y = -Math.PI / 2; // Rotation de -90 degrs autour de l'axe Y
              break;
            case 2: // Avant
              // Pas de rotation ncessaire, la face avant est dj aligne
              break;
            case 3: // Arrire
              miniCube.rotation.y = Math.PI; // Rotation de 180 degrs autour de l'axe Y
              break;
          }
          scene.add(miniCube);

          // Ajouter des bordures noires autour de chaque mini-cube
          const edgesGeometry = new THREE.EdgesGeometry(miniCubeGeometry);
          const edgesMaterial = new THREE.LineBasicMaterial({ color: 0x000000 }); // Noir
          const edges = new THREE.LineSegments(edgesGeometry, edgesMaterial);
          edges.position.copy(miniCube.position);
          edges.rotation.copy(miniCube.rotation); // Aligner les bordures avec le mini-cube
          scene.add(edges);
        });

        petitsCubesPositions.push({ x: petitCubePosX, y: petitCubePosY, z: petitCubePosZ });
      }
    }







    const handleResize = () => {
      const width = window.innerWidth;
      const height = window.innerHeight;
      renderer.setSize(width, height);
      camera.aspect = width / height;
      camera.updateProjectionMatrix();
    };

    window.addEventListener('resize', handleResize);

    const handlePointerLockChange = () => {
      if (document.pointerLockElement === mountRef.current ||
        document.mozPointerLockElement === mountRef.current ||
        document.webkitPointerLockElement === mountRef.current) {
        console.log("Pointer Lock activ");
      } else {
        console.log("Pointer Lock dsactiv");
      }
    };

    const handleMouseMove = (event) => {
      const deltaX = event.movementX || event.mozMovementX || event.webkitMovementX || 0;
      const deltaY = event.movementY || event.mozMovementY || event.webkitMovementY || 0;
      const mouseSensitivity = 0.01;

      mouse.current.x -= deltaX * mouseSensitivity;
      mouse.current.y -= deltaY * mouseSensitivity;
      mouse.current.y = THREE.MathUtils.clamp(mouse.current.y, -Math.PI / 2 + 0.1, Math.PI / 2 - 0.1);

      const euler = new THREE.Euler(mouse.current.y, mouse.current.x, 0, 'YXZ');
      camera.quaternion.setFromEuler(euler);
    };

    function estDansUnDesPetitsCubes(position, petitsCubes, petitCubeWidth, petitCubeHeight, petitCubeDepth) {
      const demiLargeur = petitCubeWidth / 2;
      const demiHauteur = petitCubeHeight / 2;
      const demiProfondeur = petitCubeDepth / 2;

      return petitsCubes.some(petitCube => {
        return position.x > petitCube.x - demiLargeur && position.x < petitCube.x + demiLargeur &&
          position.y > petitCube.y - demiHauteur && position.y < petitCube.y + demiHauteur &&
          position.z > petitCube.z - demiProfondeur && position.z < petitCube.z + demiProfondeur;
      });
    }






    function updateCameraPosition() {
      const speed = 1;
      let direction = new THREE.Vector3();
      camera.getWorldDirection(direction);
      direction.y = 0; // Ignorer la composante verticale
      direction.normalize();

      const upVector = new THREE.Vector3(0, 1, 0);
      let lateralMove = new THREE.Vector3().crossVectors(upVector, direction).normalize().multiplyScalar(speed);
      let newPosition = camera.position.clone();

      if (keysPressed['ArrowUp']) {
        newPosition.add(direction.multiplyScalar(speed));
      }
      if (keysPressed['ArrowDown']) {
        newPosition.add(direction.multiplyScalar(-speed));
      }
      if (keysPressed['ArrowLeft']) {
        newPosition.add(keysPressed['ArrowDown'] ? lateralMove.multiplyScalar(-1) : lateralMove);
      }
      if (keysPressed['ArrowRight']) {
        newPosition.add(keysPressed['ArrowDown'] ? lateralMove : lateralMove.multiplyScalar(-1));
      }

      // Empcher de traverser le petit cube
      if (!estDansUnDesPetitsCubes(newPosition, petitsCubesPositions, petitCubeWidth, petitCubeHeight, petitCubeDepth)) {
        const margin = 1; // La marge entre la camra et le mur
        newPosition.x = THREE.MathUtils.clamp(newPosition.x, -cubeWidth / 2 + margin, cubeWidth / 2 - margin);
        newPosition.z = THREE.MathUtils.clamp(newPosition.z, -cubeDepth / 2 + margin, cubeDepth / 2 - margin);
      } else {
        // Si on est dans le petit cube, on bloque le mouvement
        newPosition.copy(camera.position);
      }

      camera.position.copy(newPosition);
    }





    mountRef.current.addEventListener('click', () => {
      mountRef.current.requestPointerLock = mountRef.current.requestPointerLock ||
        mountRef.current.mozRequestPointerLock ||
        mountRef.current.webkitRequestPointerLock;
      mountRef.current.requestPointerLock();
    });

    document.addEventListener('pointerlockchange', handlePointerLockChange, false);
    document.addEventListener('mozpointerlockchange', handlePointerLockChange, false);
    document.addEventListener('webkitpointerlockchange', handlePointerLockChange, false);

    window.addEventListener('keydown', updateCameraPosition);
    window.addEventListener('mousemove', handleMouseMove);

    const KeyDown = (event) => {
      keysPressed[event.key] = true;
    };

    const KeyUp = (event) => {
      delete keysPressed[event.key];
    };

    window.addEventListener('keydown', KeyDown);
    window.addEventListener('keyup', KeyUp);

    // Navigation mobile

    const handleTouchStart = (event) => {
      touchStart.x = event.touches[0].pageX;
      touchStart.y = event.touches[0].pageY;
    };

    const handleTouchMove = (event) => {
      event.preventDefault(); // Pour viter le dfilement de la page sur les appareils mobiles
      touchCurrent.current.x = event.touches[0].pageX;
      touchCurrent.current.y = event.touches[0].pageY;

      const deltaX = touchCurrent.current.x - touchStart.current.x;
      const deltaY = touchCurrent.current.y - touchStart.current.y;

      if (event.touches.length === 1) {
        // Dplacement: modifier la position de la camra
        camera.position.x -= deltaX * 0.02; // Augmentation de la vitesse de dplacement latral
        camera.position.z += deltaY * 0.02; // Utiliser Z pour le mouvement avant/arrire, inverser pour que cela corresponde au mouvement naturel du doigt
      } else if (event.touches.length > 1) {
        // Rotation: modifier l'orientation de la camra
        camera.rotation.y -= deltaX * 0.005;
        camera.rotation.x -= deltaY * 0.005;
      }

      touchStart.current.x = touchCurrent.current.x;
      touchStart.current.y = touchCurrent.current.y;
    };

    mountRef.current.addEventListener('touchstart', handleTouchStart, { passive: false });
    mountRef.current.addEventListener('touchmove', handleTouchMove, { passive: false });



    // fin navitation mobile 

    const animate = () => {
      requestAnimationFrame(animate);
      updateCameraPosition();
      renderer.render(scene, camera);
    };

    animate();




    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('keydown', updateCameraPosition);
      document.removeEventListener('pointerlockchange', handlePointerLockChange);
      document.removeEventListener('mozpointerlockchange', handlePointerLockChange);
      document.removeEventListener('webkitpointerlockchange', handlePointerLockChange);
      mountRef.current.removeEventListener('touchstart', handleTouchStart);
      mountRef.current.removeEventListener('touchmove', handleTouchMove);
      if (mountRef.current) {
        mountRef.current.removeChild(renderer.domElement);
      }
    };
  }, [images]);

  return <div ref={mountRef} />;
}

export default ThreeScene;









